import { AuthenticationState } from "./AuthenticationState";
import ICodeName from "./models/ICodeName";
import ICreateEmailArchiveRequest from "./models/ICreateEmailArchiveRequest";
import IDocumentResponse from "./models/IDocumentResponse";
import IIdName from "./models/IIdName";
import ISession from "./models/ISession";
import { MultiDelegate } from "./MultiDelegate";
import { getSettings } from "./RoamingSettings";

export class ApiClient {

    private readonly invokeRequestFailed: (statusCode: number) => void;
    private readonly authState: AuthenticationState;

    public readonly onRequestFailed: MultiDelegate<number>;

    public constructor(authState: AuthenticationState) {
        this.authState = authState;
        const { delegate, invoke } = MultiDelegate.create<number>();
        this.invokeRequestFailed = invoke;
        this.onRequestFailed = delegate;
    }

    public getCategories(database: string): Promise<Array<IIdName>> {
        console.info("getting categories");

        console.info("getting object types");

        const query = "database=" + database;
        const url = "/categories?" + query;
        return this.get<Array<IIdName>>(url);
    }

    public getObjectTypes(database: string): Promise<Array<IIdName>> {
        console.info("getting object types");

        const query = "database=" + database;
        const url = "/objecttypes?" + query;
        return this.get<Array<IIdName>>(url);
    }

    public getActiveSessions(): Promise<Array<ISession>> {
        console.info("getting active sessions");

        return this.get<Array<ISession>>("/users/current/sessions");
    }

    public getBusinessPartnersByMail(database: string, address: string): Promise<Array<ICodeName>> {
        console.info("getting business partners by mail");

        const query = `database=${database}`;
        const url = `/businesspartners/${address}?` + query;

        return this.get<Array<ICodeName>>(url);
    }

    public getBusinessPartners(database: string, type?: number): Promise<Array<ICodeName>> {
        console.info("getting business partners");

        const query = `database=${database}&type=${type}`;
        const url = "/businesspartners?" + query;

        return this.get<Array<ICodeName>>(url);
    }

    // code is the card code of the business partner
    public getDocumentsOfBusinessPartner(database: string, code: string, type?: number): Promise<Array<IDocumentResponse>> {
        console.info("getting documents for business partner");

        const query = `database=${database}`;
        const url = `/businesspartners/${code}/documents/${type}?` + query;

        return this.get<Array<IDocumentResponse>>(url);
    }

    public updateExchangeToken(exchangeToken: string): Promise<void> {
        console.info("Updating exchange token");

        const url = "/users/current/exchangeToken";

        return this.update(url, { exchangeToken: exchangeToken });
    }

    public async createEmailArchive(request: ICreateEmailArchiveRequest): Promise<number> {

        console.info("Creating archive for email");
        const url = "/archives/emails";
        const result = await this.create(url, request) as { id: number };
        return result?.id;
    }

    public async getDocumentOfArchive(archiveId: number, database: string) : Promise<IDocumentResponse> {
        console.info("Getting document of archive", { archiveId });
        const url = `/archives/${archiveId}/document?database=${database}`;
        const result = await this.get(url) as IDocumentResponse;
        return result;
    }

    private async get<T>(relativeUrl: string): Promise<T> {

        const { access_token } = this.authState.getTokens();
        let { api, accessToken } = getRequirements();
        accessToken ??= access_token;
        if (!api || !accessToken) {
            console.error(
                "api address or access token is not provided can't execute get for ", relativeUrl);
            return null;
        }

        const url = new URL(relativeUrl, api);
        let response: Response;
        try {
            response = await fetch(url.toString(), {
                method: "GET",
                headers: {
                    Authorization: "Bearer " + accessToken,
                },
            });

            if (!response.ok) {
                this.invokeRequestFailed(response.status);
            }

            return (await response.json()) as T;
        } catch (error) {
            console.error(error);
            return null;
        }
    }

    private async update<TValue>(relativeUrl: string, value: TValue): Promise<void> {

        const { access_token } = this.authState.getTokens();
        let { api, accessToken } = getRequirements();
        accessToken ??= access_token;
        if (!api || !accessToken) {
            console.error(
                "api address or access token is not provided can't execute update for ", relativeUrl);
            return null;
        }

        const url = new URL(relativeUrl, api);
        let response: Response;
        try {
            response = await fetch(url.toString(), {
                method: "PUT",
                headers: {
                    Authorization: "Bearer " + accessToken,
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(value)
            });

            if (!response.ok) {
                this.invokeRequestFailed(response.status);
            }

            return;
        } catch (error) {
            console.error(error);
            return;
        }
    }


    private async create<TResult, TValue>(relativeUrl: string, value: TValue): Promise<TResult> {

        const { access_token } = this.authState.getTokens();
        let { api, accessToken } = getRequirements();
        accessToken ??= access_token;
        if (!api || !accessToken) {
            console.error(
                "api address or access token is not provided can't execute create for ", relativeUrl);
            return null;
        }

        const url = new URL(relativeUrl, api);
        let response: Response;
        try {
            response = await fetch(url.toString(), {
                method: "POST",
                headers: {
                    Authorization: "Bearer " + accessToken,
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(value)
            });

            if (!response.ok) {
                this.invokeRequestFailed(response.status);
            }

            // not needed to be awaited if TResult is void but idk how to change that
            return await response.json() as TResult;
        } catch (error) {
            console.error(error);
            return null;
        }
    }


}


// gets requirements for api access
function getRequirements(): { api: string; accessToken: string } {
    console.info("Getting required settings");
    const settings = getSettings();
    console.info("Loaded settings", settings);
    return { api: settings?.api, accessToken: settings?.accessToken,};
}
