import { ITokens } from "./ITokens";

const dialogSessions = "dialogSessions";
const cksTokens = "cks.tokens";

const keys = { dialogOptions: dialogSessions, cksTokens, };

export default keys;

export function setTokens(tokens?: ITokens) {
    set<ITokens>(cksTokens, tokens);
}

export function getTokens(): ITokens {
    return get<ITokens>(cksTokens);
}

function set<TValue>(key: string, value: TValue) {
    if (typeof value === "string") {
        localStorage.setItem(key, value);
        return;
    }

    const json = JSON.stringify(value);
    localStorage.setItem(key, json);
}

// do not use this to get strings
function get<TValue>(key: string): TValue {
    return JSON.parse(localStorage.getItem(key)) as TValue
}