// only supported in Outlook 2019 and newer
const isCategoriesSupported = () => {

    const isSupported = Office.context.requirements.isSetSupported("Mailbox", "1.8");
    console.info("Categories is supported: ", { isSupported });
    return isSupported;
}
export let isArchivedCategory: Office.CategoryDetails;

export async function tryInitializeCategories(): Promise<boolean> {

    isArchivedCategory = isArchivedCategory ? isArchivedCategory : {
        displayName: "Archived with cks.DIGITAL 4.0",
        color: Office.MailboxEnums.CategoryColor.Preset7
    };


    const masterCategoriesToAdd: Office.CategoryDetails[] = [
        isArchivedCategory,
    ];

    console.info("Initializing categories");
    if (!isCategoriesSupported) return false;


    const existingCategories = await getMasterCategories();
    const missingCategories = masterCategoriesToAdd
        // only get categories that are not in existing categories
        .filter(category => existingCategories.every(existing => existing.displayName !== category.displayName));
    if (missingCategories.length === 0) return true;

    const promise = new Promise<Office.AsyncResult<void>>(resolve => {
        try {
            Office.context.mailbox.masterCategories.addAsync(missingCategories, {}, resolve);
        } catch (error) {
            console.error("Error while adding master categories", error);
            throw error;
        }
    });

    const result = await promise;

    if (result.status === Office.AsyncResultStatus.Failed) {
        console.error("Failed setting master categories", result.error);
        return false;
    }

    console.info("Successfully added categories to mater list");

    return true;
}

