let resolveOnReady: () => void;
const onReady = new Promise<void>(resolve => {
  resolveOnReady = resolve;
})

Office.onReady(() => {
  // If needed, Office.js is ready to be called
  console.info("Office is ready");
  resolveOnReady();
});

async function initializeAction(): Promise<{ isInitialized: boolean, api?: ApiClient }> {

  console.info("Awaiting on ready");

  await onReady;

  const state = AuthenticationState.create();

  console.info("Checking if authenticated");

  if (!await state.isAuthenticated()) {
    const signInMessage: Office.NotificationMessageDetails = {
      type: Office.MailboxEnums.ItemNotificationMessageType.ErrorMessage,
      message: "Please open \"Archive behind document\" to sign in",
    };

    await Notifications.replace(actionKey, signInMessage);

    return { isInitialized: false };
  }

  return { isInitialized: true, api: new ApiClient(state) }
}
/**
 * tries to create an archive and show notifications if it failed or succeeded
 * also sets the custom properties using the properties object provided
 *
 * @param request the request to send used to create an archive
 * @param api the api client used to send the archive request
 * @param properties the properties object to add the archived custom properties to the mail
 * @param archivedInDatabases the databases the mail was already archived in
 * @param createSuccessMessage a function to resolve the success message in case the archiving was successful
 * @returns a boolean indicating if the archiving was successful
 */
async function tryCreateArchive(request: ICreateEmailArchiveRequest, api: ApiClient, properties: customProperties, archivedInDatabases: IDatabaseArchive[], createSuccessMessage: (archiveId: number, request: ICreateEmailArchiveRequest) => string): Promise<boolean> {

  const archiveId = await api.createEmailArchive(request);
  if (!archiveId) {
    console.error("Creating archive did not return the new id which indicates the creation of the archive was not successful");
    const errorMessage: Office.NotificationMessageDetails = {
      type: Office.MailboxEnums.ItemNotificationMessageType.ErrorMessage,
      message: "Sorry, could not create an archive for this mail",
    };

    await Notifications.replace(actionKey, errorMessage);
    return false;
  }


  // update archived in databases for mail
  archivedInDatabases.push({ database: request.archive.database, archiveId });
  const propertiesPromise = properties.update<IArchiveProperty>(archiveDetails, {
    archivedInDatabases: archivedInDatabases
  });


  // try set category if supported by this outlook client
  const categoriesPromise = tryInitializeCategories()
    .then(isInitialized => {
      if (isInitialized)
        tryAddCategoriesToItem(isArchivedCategory);
    });

  await Promise.all([propertiesPromise, categoriesPromise]);

  const successMessage: Office.NotificationMessageDetails = {
    type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
    message: createSuccessMessage(archiveId, request),
    icon: "Icon.80x80",
    persistent: true,
  };

  await Notifications.replace(actionKey, successMessage);

  return true;
}

async function showChooseOptionDialog(options: IOption[]): Promise<string> {

  // transmit companies where not archived yet to dialog
  const messageToChild = JSON.stringify(options);

  // cannot use messageChild api since that is requirement set Mailbox 1.9 
  // and I need to wait until office is ready in dialog until I can send messages to dialog
  // that would require initiating dialog > waiting till office is initalized in dialog > dialog registers event to listen to messages from paren > dialog messages parent that its listening > parent sends data to child > ... 
  // check for requirement set and use messageChild


  // use local storage instead
  localStorage.setItem(keys.dialogOptions, messageToChild);
  /*  localstorage seems to not work from one day to another on windows outlook (365) client.
      After opening the dialog the localStorage is empty.
      even though it seems to be the same origin. Need to investigate again later.
      But until then send databases in query (I would really prefer not to send the databases through the network)
  */

  // sessions to query (I would really prefer not to)

  const query = new URLSearchParams(options.map(({ key, value }) => [key, value]))
  const location = window.location.origin + "/dialog.html?" + query.toString();
  console.info("Opening dialog at ", location);
  // awaits only the opening not the closing
  const dialog = await displayDialog(location, { height: 30, width: 20, displayInIframe: true })
  console.info({ dialog });

  // create promise that is resolved when the dialog closes or a database is chosen
  const databaseSelectedPromise = new Promise<{ message: string | boolean } | { error: number }>(resolve => {
    dialog.addEventHandler(Office.EventType.DialogEventReceived, resolve);
    dialog.addEventHandler(Office.EventType.DialogMessageReceived, resolve);
  });


  console.info("Awaiting database to be selected");
  const argument = await databaseSelectedPromise;

  console.info("Awaiting completed");
  const key = "message" in argument ? argument.message.toString() : null;


  if ("error" in argument)
    switch (argument.error) {
      case 12002:
        let message = "The dialog box has been directed to a page that it cannot find or load, or the URL syntax is invalid.";
        console.error(message);
        break;
      case 12003:
        message = "The dialog box has been directed to a URL with the HTTP protocol. HTTPS is required.";
        console.error(message);
        break;
      case 12006:
        console.info("Dialog closed.");
        break;
      default:
        message = "Unknown error in dialog box.";
        console.error(message);
        break;
    }

  console.info("Dialog was closed with", { key });

  // don't call close if it was closed by user (then key is null)
  if (key)
    dialog.close();

  // clean up because no longer needed
  localStorage.removeItem(keys.dialogOptions);
  return key;
}

/**
 * Runs an archive action for the current mail that can be modified the function parameters
 */
async function runArchiveAction<TSessionContext extends { session: ISession }>
  (
    event: Office.AddinCommands.Event,
    getValidSessions: (api: ApiClient, sessions: ISession[]) => Promise<TSessionContext[]>,
    getArchiveIdentifier: (chosenContext: TSessionContext) => Promise<IArchiveIdentifier>,
    generateSuccessMessage: (params: ISuccessMessageParams) => string
  ) {
  try {
    const { isInitialized, api } = await initializeAction();

    if (!isInitialized) {
      event.completed();
      return;
    }

    let sessions = await api.getActiveSessions();
    if (!sessions || sessions.length === 0) {
      console.error("No sessions to archive", { sessions });
      const noSessionMessage: Office.NotificationMessageDetails = {
        type: Office.MailboxEnums.ItemNotificationMessageType.ErrorMessage,
        message: "Sorry, could not get databases to archive behind. Please check if you can sign in."
      }

      await Notifications.replace(actionKey, noSessionMessage);
      event.completed();
      return;
    }


    // get companies that are already archived behind
    const properties = await customProperties.load();
    const archivedInDatabases = properties.get<IArchiveProperty>(archiveDetails)?.archivedInDatabases ?? [];

    if (archivedInDatabases.length === 0)
      sessions = sessions
        // not in these databases where already archived
        .filter(session => archivedInDatabases.every(({ database }) => session.database !== database));

    // if no company is left show message no company + "you can add more in settings"

    const noSessionMessage: Office.NotificationMessageDetails = {
      type: Office.MailboxEnums.ItemNotificationMessageType.InformationalMessage,
      icon: "Icon.80x80",
      message: "No company found. You can add more companies under \"Archive behind document\"",
      persistent: true,
    };

    if (sessions.length === 0) {
      await Notifications.replace(actionKey, noSessionMessage);
      event.completed();
      return;
    }

    // get valid sessions if function is provided otherwise all sessions are valid
    const valid = await getValidSessions(api, sessions);
    // if null then it was handled by getValidSessions function
    if (!valid) {
      event.completed();
      return;
    }
    if (valid.length === 0) {
      console.error("No valid session. Need at least one valid session");

      await Notifications.replace(actionKey, noSessionMessage);
      event.completed();
      return;
    }

    let options: IOption[] = valid.map(result => ({ key: result.session.database, value: result.session.companyName }));
    const database = await showChooseOptionDialog(options);
    // if null dialog was closed by user which is the same as cancelling for us
    if (!database) {
      console.info("Dialog closed by user without selecting a database");
      // don't need to remove action manually since outlook already removes it when calling event.completed()
      // await Notifications.remove(actionKey);
      event.completed();
      return;
    }

    console.info("User selected database", { database });
    const selectedCompany = sessions.find(session => session.database === database).companyName;
    const message: Office.NotificationMessageDetails = {
      type: Office.MailboxEnums.ItemNotificationMessageType.ProgressIndicator,
      message: `Archiving email in company ${selectedCompany}.`,
    };

    Notifications.replace(actionKey, message);

    // get chosen context
    const chosenContext = valid.find(({ session }) => session.database === database);

    const email = await Item.getEmailIdentifier();
    // the as is cheating but if no method to generate session contexts is supplied then no other properties on session context should be expected
    const archive = await getArchiveIdentifier(chosenContext);
    if (!archive) {
      event.completed();
      return;
    }

    await tryCreateArchive({ archive, email }, api, properties, archivedInDatabases, (archiveId, request) => generateSuccessMessage({ archiveId, request, session: chosenContext.session }));
    event.completed();
  } catch (error) {
    console.error(error);
    const exceptionMessage: Office.NotificationMessageDetails = {
      type: Office.MailboxEnums.ItemNotificationMessageType.ErrorMessage,
      // message: "Sorry, something went wrong"
      message: JSON.stringify(error)
    }

    await Notifications.replace(actionKey, exceptionMessage);
  }

}

async function archiveBehindBusinessPartner(event: Office.AddinCommands.Event) {

  const getValidSessions = async (api: ApiClient, sessions: ISession[]) => {
    const address = Office.context.mailbox.item.sender.emailAddress;
    console.info("Getting business partners that match address from sessions", { address, sessions });
    const getAddressesPromisses = sessions.map(async (session) => {
      const partners = await api.getBusinessPartnersByMail(session.database, address)
      return { session, partners };
    });

    const results = await Promise.all(getAddressesPromisses);
    console.info("Got the following results", { results });

    // only use companies that have a business partner
    const valid = results.filter(result => result?.partners && result.partners.length !== 0);
    if (valid.length === 0) {
      console.info("Got no business partner with matching email address");

      const notFoundMessage: Office.NotificationMessageDetails = {
        type: Office.MailboxEnums.ItemNotificationMessageType.ErrorMessage,
        message: `No business partner with address ${address} found`
      }

      await Notifications.replace(actionKey, notFoundMessage);
      return null;
    }

    return valid;
  }

  const getArchiveIdentifier = async (chosenContext: { session: ISession, partners: ICodeName[] }): Promise<IArchiveIdentifier> => {
    const options = chosenContext.partners.map(({ code, name }) => ({ key: code, value: `${code} - ${name}` }));
    const code = await showChooseOptionDialog(options);

    // handle not selected / closed by user
    if (!code) {
      console.info("Dialog closed by user without selecting a business partner");
      return null;
    }

    return {
      database: chosenContext.session.database,
      key1: code,
      // business partners (should swap with enum at some point)
      type: 2,
      businessPartnerCode: code
    }
  }
  // const generateSuccessMessage = (archiveId: number, request: ICreateEmailArchiveRequest, context: { session: ISession }) => `Successfully archived mail for ${context.session.companyName} with archiveId ${archiveId}`;
  const generateSuccessMessage: (params: ISuccessMessageParams) => string = ({ archiveId, request, session }) => `Successfully archived mail for ${session.companyName} with archiveId ${archiveId} behind business partner ${request.archive.key1}`;


  await runArchiveAction(event, getValidSessions, getArchiveIdentifier, generateSuccessMessage);
}

async function archiveInInbox(event: Office.AddinCommands.Event) {
  // just return the sessions
  const getValidSessions = (_: ApiClient, sessions: ISession[]) => Promise.resolve(sessions.map(session => ({ session })));
  const getArchiveIdentifier = async (chosenContext: { session: ISession }): Promise<IArchiveIdentifier> => ({
    database: chosenContext.session.database,
    // inbox
    type: 0,
    key1: "",
  });

  const generateSuccessMessage: (params: ISuccessMessageParams) => string = ({ archiveId, session }) => `Successfully archived mail for ${session.companyName} with archiveId ${archiveId} in inbox`;

  await runArchiveAction(event, getValidSessions, getArchiveIdentifier, generateSuccessMessage);
}

interface ISuccessMessageParams {
  archiveId: number;
  request: ICreateEmailArchiveRequest;
  session: ISession;
}


function getGlobal() {
  return typeof self !== "undefined"
    ? self
    : typeof window !== "undefined"
      ? window
      : typeof global !== "undefined"
        ? global
        : undefined;
}

const g = getGlobal() as any;

// the add-in command functions need to be available in global scope
g.archiveInInbox = archiveInInbox;
g.archiveBehindBusinessPartner = archiveBehindBusinessPartner;
