import Mailbox from "./Mailbox";
import { IEmailIdentifier } from "./models/ICreateEmailArchiveRequest";


export function getItemId(): string {

    const hostName = Office.context.mailbox.diagnostics.hostName;
    const itemId = Office.context.mailbox.item.itemId;
    // if is mobile
    if (hostName === "OutlookIOS" || hostName === "OutlookAndroid") {
        // itemId is already REST-formatted.
        return itemId;
    }

    // Convert to an item ID for API v2.0.
    // there is also a method available for converting to ews id
    return Office.context.mailbox.convertToRestId(itemId, Office.MailboxEnums.RestVersion.v2_0);
}
export async function getEmailIdentifier(): Promise<IEmailIdentifier> {
    const tokenPromise = Mailbox.getCallBackToken();

    const itemId = getItemId();
    const restUrl = Office.context.mailbox.restUrl;
    const token = await tokenPromise;
    const sender = Office.context.mailbox.item?.sender.emailAddress.toLowerCase();
    const user = Office.context.mailbox.userProfile.emailAddress.toLowerCase();

    return { accessToken: token, restUrl: restUrl, itemId: itemId, isSent: sender === user };
}

const Item = { getItemId, getEmailIdentifier };
export default Item;
