
export class MultiDelegate<T> {
    private _callbacks = new Array<(value: T) => void>();

    // use this to subscribe to the invokation list
    public add(callback: (value: T) => void) {
        this._callbacks.push(callback);
    }

    public remove(callback: (value: T) => void) {
        const index = this._callbacks.indexOf(callback);
        if (index > -1)
            this._callbacks.splice(index, 1);
    }

    public static create<T>(): { delegate: MultiDelegate<T>; invoke: (value: T) => void; } {
        const delegate = new MultiDelegate<T>();
        const invoke = (value: T) => delegate._callbacks.forEach((callback) => callback(value));

        return { delegate: delegate, invoke: invoke };
    }
}

