export const archiveDetails = "archiveDetails"
export default class customProperties {
    private _properties: Office.CustomProperties;
    private constructor(properties: Office.CustomProperties) {
        this._properties = properties;
    }

    public static async load(): Promise<customProperties> {
        const promise = new Promise<Office.AsyncResult<Office.CustomProperties>>(resolve => Office.context.mailbox.item.loadCustomPropertiesAsync(resolve));

        const result = await promise;
        if (result.status === Office.AsyncResultStatus.Failed) {
            console.error(result.error);
            return null;
        }

        return new customProperties(result.value);
    }

    public get<TValue>(name: string): TValue {
        return this._properties.get(name) as TValue;
    }

    public update<TValue>(name: string, value: TValue): Promise<void> {
        // documentation on method says value can be of any type but it is typed as string
        // propably because it will be transfered as string
        this._properties.set(name, value as any);

        return new Promise<void>((resolve, reject) => {
            this._properties.saveAsync(result =>
                result.status === Office.AsyncResultStatus.Failed
                    ? reject(result.error)
                    : resolve()
            );
        });
    }
}