export function displayDialog(startAddress: string, options? : Office.DialogOptions) : Promise<Office.Dialog>{
    return new Promise<Office.Dialog>((resolve, reject) => {
        Office.context.ui.displayDialogAsync(startAddress, options, result => {
            if (result.status === Office.AsyncResultStatus.Succeeded) {
                resolve(result.value);
                return;
            }

            console.error("Error while openen dialog", result.error);
            reject(result.error);
        });
    })
}