
const settingsName = "Settings";
export interface ISettings {
  accessToken: string;
  refreshToken: string;
  api: string;
}
export function getSettings(): ISettings {
  console.info("Getting roaming settings");
  const settings = Office.context.roamingSettings;
  let result = settings.get(settingsName);
  console.info("Got roaming settings", { result });
  if (!result) return null;

  result = JSON.parse(result) as ISettings;
  console.info("Loaded roaming settings", result);

  return result as ISettings;
}

// make a promise to make the save settings saveAsync awaitable without callback
function saveAsync(): Promise<void> {
  return new Promise<void>((resolve, reject) => {
    const settings = Office.context.roamingSettings;
    console.info("Saving roaming settings");
    settings.saveAsync((result) => {
      if (result.status === Office.AsyncResultStatus.Failed) {
        console.error("Failed setting roaming settings");
        reject(result.error);
        return;
      }

      console.info("Saved roaming settings");
      resolve();
    });
  });
}

export async function setSettings(addInSettings: ISettings): Promise<void> {
  console.info("Setting roaming settings: ", addInSettings);
  const settings = Office.context.roamingSettings;

  const asString = JSON.stringify(addInSettings);
  console.info("Roaming settings as string", { asString });
  settings.set(settingsName, asString);

  await saveAsync();
}
