export function add(key: string, JSONmessage: Office.NotificationMessageDetails) {
    return new Promise<void>((resolve, reject) => {
        Office.context.mailbox.item.notificationMessages.addAsync(key, JSONmessage, {}, result => {
            if (result.status === Office.AsyncResultStatus.Succeeded) {
                resolve();
                return;
            }

            console.error("Failed adding notification", result.error);
            reject(result.error);
        });
    })
}
export function replace(key: string, JSONmessage: Office.NotificationMessageDetails) {
    console.info("Replacing notification message", { key, JSONmessage });
    return new Promise<void>((resolve, reject) => {
        Office.context.mailbox.item.notificationMessages.replaceAsync(key, JSONmessage, {}, result => {
            if (result.status === Office.AsyncResultStatus.Succeeded) {
                resolve();
                return;
            }

            console.error("Failed adding notification", result.error);
            reject(result.error);
        });
    })
}

export function remove(key: string): Promise<void> {
    return new Promise<void>((resolve, reject) => {
        Office.context.mailbox.item.notificationMessages.removeAsync(key, {}, result => {
            if (result.status === Office.AsyncResultStatus.Succeeded) {
                resolve();
                return;
            }

            console.error("Failed removing notification", { key, error: result.error });
            reject(result.error);
        })
    });
}

export const actionKey = "action";

const Notifications = { add, replace, remove };
export default Notifications;
