import { IDisposable } from "@fluentui/react";

export function logError(message: any, ...optionalParams: any[]) {
  console.error(message, ...optionalParams);
}
// export function logError(...data: any[]) {
//   console.error(...data);
// }

export const createScope = (name?: string) => new ScopedLogger(name);

export class ScopedLogger implements IDisposable {
  constructor(name?: string) {
    console.group(name);
  }

  dispose() {
    console.groupEnd();
  }
}
